export default function App(){
      

      const code = useRef(`
x = 10
def fun(a):
  print(a, x)

fun('6')
`)

    
    const [editorStyle, setEditorStyle] = useState(
        localStorage.getItem('editorStyle') || 'material-darker'
        )

    useEffect(() => {
        localStorage.setItem('editorStyle', editorStyle)
    }, [editorStyle])

    return (<React.Fragment>
    <button onClick={() => console.log(code.current)}>
        attack!
    </button>
    <select value={editorStyle} onChange={event => setEditorStyle(event.target.value)}>
        {editorStyleOptions.map(style => <option value={style} key={style}>{style}</option>)}
    </select>
<CodeMirror
  value={code.current}
  onChange={event => code.current = event.getValue()}
  options={{
    theme: editorStyle,
    tabSize: 2,
    keyMap: 'sublime',
    mode: 'python',
  }}
/>
</React.Fragment>
)
}